"ues es6";

import { csv, select, arc, range, selectAll, max, scaleLinear } from "d3";
import Rose from "./Rose";

const labels = ["disease", "injury", "other"];

function addLegends() {
  // Color Legend
  const legends = select(".controls")
    .append("svg:svg")
    .attr("class", ".legend")
    .attr("width", 100)
    .attr("height", 100)
    .selectAll(".label")
    .data(labels)
    .enter()
    .append("svg:g")
    .attr("class", ".label")
    .attr("transform", (d, i) => `translate(5, ${20 + i * 30})`);

  legends
    .append("svg:circle")
    .attr("class", d => `${d}`)
    .attr("r", "5")
    .attr("cx", "0")
    .attr("cy", "0");

  legends
    .append("rect")
    .attr("opacity", 0)
    .attr("fill", "grey")
    .attr("width", 75)
    .attr("height", 30)
    .attr("transform", "translate(-5, -15)");

  legends
    .append("text")
    .text(d => d)
    .attr("transform", "translate(10, 5)");

  function mouseOver() {
    // Select the current element and get the symbol child class:
    const legend = select(this)
      .select("circle")
      .attr("class");

    select(this)
      .select("rect")
      .transition()
      .duration(500)
      .attr("opacity", 0.15);

    selectAll(".wedge")
      .filter(function() {
        return !select(this).classed(legend);
      })
      .transition()
      .duration(500)
      .attr("opacity", 0.15);

    select(this);
  }

  const mouseOut = () => {
    selectAll(".wedge")
      .transition()
      .duration(100)
      .attr("opacity", 1);

    selectAll("rect")
      .transition()
      .duration(100)
      .attr("opacity", 0);
  };

  legends.on("mouseover", mouseOver).on("mouseout", mouseOut);
}

// Add a control slider for a given chart
function addSlider(chart, minVal, maxVal) {
  select(".controls")
    .append("input")
    .attr("class", "slider")
    .attr("type", "range")
    .attr("name", "slider")
    .attr("min", minVal)
    .attr("max", maxVal)
    .attr("step", 1)
    .attr("value", minVal);

  select("input").on("input", function() {
    chart.redraw(1024 - this.value);
  });
}

function main() {
  csv("../data/nightingale-data.csv", d => {
    // shape the data as we read it in
    return {
      month: d.Month,
      rates: [+d.disease, +d.injury, +d.other]
    };
  }).then(data => {
    // display and draw the example small rose.
    // We don't add controls to this rose, so we draw it right away
    new Rose(select(".example-container"), data.slice(9, 21), 120).draw();

    // initialize our controllable rose
    const mainRose = new Rose(
      select(".graph-container"),
      data.slice(0, 12),
      240
    );

    // add legend controls
    addLegends();

    // Adding a slider for zoom to the main chart
    addSlider(mainRose, 1, 1023);

    // After adding control layers for the main rose, we draw it
    mainRose.draw();
  });
}

main();
