"use es6";

import { scaleLinear, max, range, arc, select, selectAll } from "d3";

// A class for generating a Rose graph.
export default class Rose {
  constructor(rootSelector, data, maxRadius, rotationOffset = 0) {
    // constants
    this.rootSelector = rootSelector; // Root selector is the parent we append the graph to
    this.maxRadius = maxRadius;
    this.width = maxRadius * 2 + 20;
    this.height = maxRadius * 2 + 20;
    this.radiusScale = scaleLinear([0, this.maxRadius]);
    this.segmentAngle = Math.PI / 6;
    this.rotationOffset = rotationOffset; // Rotation offset is used to rotate the graph in radians
    this.labels = ["disease", "injury", "other"];

    // process input data
    this.data = this.processData(data);

    // Initialize arc generators
    this.wedgeArc = arc()
      .innerRadius(0)
      .outerRadius((d, i) => this.radiusScale(d.rate))
      .startAngle(d => d.startAngle + this.rotationOffset)
      .endAngle(d => d.startAngle + this.segmentAngle + this.rotationOffset);

    this.labelArc = arc()
      .innerRadius(0)
      .outerRadius((d, i) => max([this.radiusScale(d.values[0].rate), 70]))
      .startAngle(d => d.values[0].startAngle + this.rotationOffset)
      .endAngle(
        d => d.values[0].startAngle + this.segmentAngle + this.rotationOffset
      );
  }

  // Creating our canvas

  // Given nightingale data, convert mortality rates into radians and radii for our rose chart.
  processData(data) {
    // Limit to first 12 months given
    data = data.slice(0, 12);

    // Finding the maximum value in the dataset to determine the scale of the max radius
    let maxValue = max(data, d => max(d.rates));
    this.radiusScale.domain([0, maxValue]);

    let processedData = [];

    data.forEach((d, i) => {
      let ids = range(0, d.rates.length);

      // Sorting an array of id's so that we know which causes of death[disease, injury, other]
      // have the larger wedges to determine the order we render them so that bigger wedges don't
      // cover smaller wedges
      ids.sort((a, b) => {
        return d.rates[b] - d.rates[a];
      });

      const startAngle = this.segmentAngle * i;
      // Returning data formatted for rendering wedges
      const sorted = ids.map(i => {
        return {
          legend: this.labels[i],
          rate: d.rates[i],
          startAngle
        };
      });
      processedData.push({ month: d.month, values: sorted });
    });

    return processedData;
  }

  // Given a new scale, redraw arcs
  redraw(scale) {
    // Update the scale with new value
    this.radiusScale.domain([0, scale]);

    // For each segment, redraw the 3 wedges with the new scale
    this.arcSegments.selectAll(".wedge").attr("d", this.wedgeArc);
    console.log(this.arcSegments.selectAll(".label-path"));
    // For each segment, redraw the monthly labels with the new scale
    this.arcSegments.selectAll(".label-path").attr("d", this.labelArc);
  }

  draw() {
    // initializing svg canvas
    this.canvas = this.rootSelector
      .append("svg:svg")
      .attr("width", this.width)
      .attr("height", this.height)
      .attr("class", "canvas");
    this.graph = this.canvas
      .append("svg:g")
      .attr("transform", `translate(${this.width / 2}, ${this.height / 2})`)
      .attr("class", "graph");

    // For each month, we have a segment
    this.arcSegments = this.graph
      .selectAll(".segment")
      .data(this.data)
      .enter()
      .append("svg:g")
      .attr("class", "segment")
      .attr("transform", "scale(1, 1)");

    // For each segment, draw the 3 wedges for the 3 death types
    this.wedges = this.arcSegments
      .selectAll(".wedge")
      .data(d => d.values)
      .enter()
      .append("path")
      .attr("class", (d, i) => `wedge ${d.legend} ${d.rate}`)
      .attr("d", this.wedgeArc)
      .attr("opacity", "1");

    // avoid clashing with existing graph
    const labelOffset = selectAll(".label-path").size();

    // Drawing an invisible path for monthly labels to follow around the chart
    this.arcSegments
      .append("svg:path")
      .attr("class", "label-path")
      .attr("id", (d, i) => `label-path${i + labelOffset}`)
      .attr("d", this.labelArc)
      .attr("fill", "none")
      .attr("stroke", "grey")
      .attr("opacity", "0.5")
      .attr("stroke-dasharray", "3")
      .attr("stroke-width", "0.5");

    // Adding Monthly labels along arc path at edge of wedges
    this.arcSegments
      .append("svg:text")
      .attr("class", "label")
      .attr("font-size", "0.5em")
      .attr("dy", "-0.5em")
      .attr("x", "5")
      .append("textPath")
      .attr("xlink:href", (d, i) => `#label-path${i + labelOffset}`)
      .text(d => d.month);

    // Adding mouse events to change opacity for some interactivity
    function mouseHover() {
      select(this)
        .transition()
        .duration(50)
        .attr("opacity", 0.5);
    }
    function mouseOff() {
      select(this)
        .transition()
        .duration(50)
        .attr("opacity", 1);
    }
    this.wedges.on("mouseover", mouseHover).on("mouseout", mouseOff);

    // Adding a small tooltip to read raw values of the data
    this.wedges.append("svg:title").text(d => `${d.legend} ${d.rate}`);
  }
}
